import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getTenures,
  tenuresEdit,
  tenuresDelete,
  isModelVisible,
  SetTenuresParams,
} from "../../../../store/Masters/Tenures/tenuresSlice";
import AddTenures from "./addTenures";

function Tenures(props) {
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  useEffect(() => {
    props.getTenures({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      key: "index",
      width: "5%",
    },
    {
      title: "Code",
      dataIndex: "code",
      key: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Tenure",
      // dataIndex: "name",
      render: (row) => <span className={columnClass}>{row.name}</span>,
      filterMode: "tree",
      filterSearch: true,
      width: "15%",
    },
    {
      title: "Sequence",
      render: (row) => <span className={columnClass}>{row.sequence}</span>,
      filterMode: "tree",
      filterSearch: true,
      key: "sequence",
      width: "15%",
    },
    {
      title: "Fixed Interest",
      render: (row) => (
        <span className={columnClass}>{row.interestfixed?.name}</span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: "15%",
    },
    {
      title: " Interest Choice",
      render: (row) => (
        <span className={columnClass}>
          {row.interestchoice.map(item => (
            item.name + ", "
          ))}
        </span>
      ),
      filterMode: "tree",
      filterSearch: true,
      width: "20%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_tenure") !== -1 && (
            <i
              className="ri-edit-box-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.tenuresEdit(row);
                props.isModelVisible(true);
              }}
            ></i>
          )}

          {props.userAccessList?.indexOf("Masters.delete_tenure") !== -1 && (
            <Popconfirm
              placement="topRight"
              title="Are you sure to delete this Record?"
              onConfirm={() => props.tenuresDelete(record.id)}
              okText="Yes"
              cancelText="No"
              className="hp-ml-8"
              icon={
                <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
              }
            >
              <i
                className="ri-delete-bin-2-line "
                style={{ cursor: "pointer", color: "#ff0000" }}
              ></i>
            </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.tenureslist.map((row, key) => ({...row, index:
      (props.tenuresParams.page - 1) * props.tenuresParams.page_size + (key + 1),
  }));

  // function onShowSizeChange(current, pageSize) {
  //   props.getCountrys({ page_size: pageSize, page: 1 });
  // }
  function onPageChange(page, pageSize) {
    props.SetTenuresParams({
      ...props.tenuresParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getTenures({
      ...props.tenuresParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetTenuresParams({ ...props.tenuresParams, search: val });
    props.getTenures({ ...props.tenuresParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Tenures Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_tenure") !== -1 &&
          "Add Tenure"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-10">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getTenures({
                      ...props.tenuresParams,
                      page: 1,
                      search: "",
                    });
                    props.SetTenuresParams({
                      ...props.tenuresParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.tenuresParams?.page}
                pageSize={props.tenuresParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddTenures />
    </Row>
  );
}

function mapStateToProps({ tenures, system }) {
  return {
    tenureslist: tenures.tenureslist,
    noofpages: tenures.noofpages,
    listCount: tenures.listCount,
    tenuresParams: tenures.tenuresParams,
    loading: tenures.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getTenures,
  tenuresEdit,
  tenuresDelete,
  isModelVisible,
  SetTenuresParams,
})(Tenures);
