import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  tenuresAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Tenures/tenuresSlice";
import { getMiniPercentageInterests } from "../../../../store/mini/miniSlice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";

const AddTenures = (props) => {
  const [form] = Form.useForm();

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  useEffect(() => {
    props.getMiniPercentageInterests();
  }, []);

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.selectedData?.name,
      sequence: props.selectedData?.sequence,
      interestfixed_id: props.selectedData?.interestfixed_id,
      interestchoice_ids: props.selectedData?.interestchoice_ids
    };
    if (obj.name != null || obj.name == "") {
      props.tenuresAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.selectedData.id,
      name: props.selectedData?.name,
      sequence: props.selectedData?.sequence,
      interestfixed_id: props.selectedData?.interestfixed_id,
      interestchoice_ids: props.selectedData?.interestchoice_ids
    };
    props.tenuresAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Tenure
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      sequence: props.selectedData.sequence,
      interestfixed_id: props.selectedData.interestfixed_id,
      interestchoice_ids: props.selectedData.interestchoice_ids
    });
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Tenure:"
              name="name"
              rules={[{ required: true, message: "Please input your Tenure!" }]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="name"
                name="name"
                style={{ width: "100%" }}
                placeholder="Tenure"
                value={
                  props.selectedData?.name == null
                    ? ""
                    : props.selectedData?.name
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Sequence :"
              name="sequence"
              rules={[
                { required: true, message: "Please input your Sequence!" },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="sequence"
                name="sequence"
                style={{ width: "100%" }}
                placeholder="Sequence"
                value={
                  props.selectedData?.sequence == null
                    ? ""
                    : props.selectedData?.sequence
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Fixed Interest :"
              name="interestfixed_id"
              rules={[
                {
                  required: true,
                  message: "Please select your Fixed Interest!",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                placeholder="Select Fixed Interest"
                optionFilterProp="Interest"
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "interestfixed_id",
                    value: value,
                  });
                }}
                value={
                  props.selectedData.interestfixed_id == null
                    ? ""
                    : props.selectedData?.interestfixed_id
                }
              >
                {props.miniPercentageInterestsList.map(
                  (interestfixed, index) => {
                    return (
                      <Option key={index} value={interestfixed.id}>
                        {interestfixed.name}
                      </Option>
                    );
                  }
                )}
              </Select>
            </Form.Item>
          </Col>

          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Interest Choice :"
              name="interestchoice_ids"
              rules={[
                {
                  required: true,
                  message: "Please select your Interest Choice!",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                mode="multiple"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                placeholder="Select Interest Choice"
                optionFilterProp="Interest"
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "interestchoice_ids",
                    value: value,
                  });
                }}
                value={
                  props.selectedData.interestchoice_ids == null
                    ? ""
                    : props.selectedData?.interestchoice_ids
                }
              >
                {props.miniPercentageInterestsList.map(
                  (interestchoice, index) => {
                    return (
                      <Option key={index} value={interestchoice.id}>
                        {interestchoice.name}
                      </Option>
                    );
                  }
                )}
              </Select>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ tenures, mini }) {
  return {
    selectedData: tenures.rowdata,
    model: tenures.model,
    miniPercentageInterestsList: mini.miniPercentageInterestsList,
  };
}

export default connect(mapStateToProps, {
  tenuresAdd,
  isModelVisible,
  InputChangeValue,
  getMiniPercentageInterests,
})(AddTenures);
